import * as React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  const handleOrder = () => {
    window.alert('Yes');
  };



    return (
      <Layout location={location} title={siteTitle}>
        <Bio />
        {/* button*/}
        <div style={{display: "flex", justifyContent: "center"}}>
        <button
        onClick={handleOrder}
        className="custom_button"
        style={{width: "500px", height: "50px",
                border: "0px", borderRadius: "10px 100px / 120px",
                color: "white", backgroundColor: "rgba(0, 176, 165, 0.5)"
                }}
        onMouseOver={(e) => {
          e.target.style.backgroundColor = "rgba(0, 176, 165, 1)";
          e.target.style.cursor = "pointer";
          e.target.style.fontSize = "20px";
        }}
        onMouseOut={(e) => {
          e.target.style.backgroundColor = "rgba(0, 176, 165, 0.5)";
          e.target.style.cursor = "default";
          e.target.style.fontSize = "16px";
        }}
        >button</button>
        
        </div>
        <header>
          <h1 itemProp="headline">{posts[0].frontmatter.title}</h1>
          <p>{posts[0].frontmatter.date}</p>
        </header>
        <section
          dangerouslySetInnerHTML={{ __html: posts[0].html }}
          itemProp="articleBody"
        />
      </Layout>
    )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="All posts" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      nodes {
        html
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
